.App{
  background-color: rgb(26, 26, 26);
  min-height: 100vh;
}
.pembatas{
  width: 100%;
  height: 70px;
}
.search-from{
  display: flex;
  min-height: 10vh;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(-225deg, #ff0da2 0%, #ff1385 52%, #ff0000 100%);
  position: fixed;
  width: 100%;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 10px 0px 0px 10px;
  background-color: rgb(24, 24, 24);
  color: white;
  box-shadow:0px 10px 70px rgb(0, 0, 0) inset;
}

.search-button{
  background-color: rgb(15, 15, 15);
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 0px 10px 10px 0px;
}



.info{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}