.info{
    border-radius: 10px;
    /*box-shadow: 5px 5px 18px rgb(255, 0, 98); */
    background-color: rgb(255, 0, 98);
    color: white;
    display: flex;
    margin: 20px;
    flex-direction: column;
    
    align-items: center;
    width: 400px;
    overflow-y: scroll;
    min-height: 700px ;
}

.title{
  text-align: center;
  overflow: hidden;
}
.off{
    width: 300px;
    height: 150px;
    overflow: hidden;
    border-radius: 20px;

}
.image{
    width: 300px;
    height: 300px;
    margin: -50px 0 0 0px;
}

/* width */
::-webkit-scrollbar {
    width: 20px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
     
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(41, 41, 41); 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
  }